.simple-division {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.division-content {
  display: flex;
  flex-direction: row;
}
.difficultySelect {
  display: flex;
  align-items: center;
  margin-left: 2rem;
}
.answer-section,
.answer {
  display: flex;
  flex-direction: row;
}
.answer {
  margin-left: 1rem;
  justify-content: space-between;
  align-items: center;
}
.answer > input {
  margin: 0 0.5rem;
  /* width: 3rem; */
  padding: 5px 5px;
  height: 1.2rem;
  border-radius: 3px;
  border: 1px solid #ccc;
  text-align: center;
}

.division-answer-submit {
  background-color: #4caf50;
  color: white;
  padding: 5px 5px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@media only screen and (max-device-width: 480px) {
    .answer{
        flex-direction: column;
    }
    .answer input {
        margin-bottom: 1rem
    }
}